document.addEventListener('DOMContentLoaded', () => {
    const iosVideos = document.querySelectorAll('[data-ios-video="ios-video"]')

    iosVideos.forEach((video) => {
        // ÐžÑ‚ÐºÐ»ÑŽÑ‡Ð°ÐµÐ¼ Ð²Ð·Ð°Ð¸Ð¼Ð¾Ð´ÐµÐ¹ÑÑ‚Ð²Ð¸Ðµ Ð¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÐµÐ»Ñ
        video.style.pointerEvents = 'none'

        // ÐŸÑ€Ð¸Ð½ÑƒÐ´Ð¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾Ðµ Ð²Ð¾ÑÐ¿Ñ€Ð¾Ð¸Ð·Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ Ñ‡ÐµÑ€ÐµÐ· user gesture ÑÐ¸Ð¼ÑƒÐ»ÑÑ†Ð¸ÑŽ
        const tryPlay = () => {
            video.play().catch(() => {
                // Ð•ÑÐ»Ð¸ Ð½Ðµ Ð¿Ð¾Ð»ÑƒÑ‡Ð¸Ð»Ð¾ÑÑŒ, Ð¿Ñ€Ð¾Ð±ÑƒÐµÐ¼ ÑÐ½Ð¾Ð²Ð° Ð¿Ð¾ÑÐ»Ðµ Ð²Ð·Ð°Ð¸Ð¼Ð¾Ð´ÐµÐ¹ÑÑ‚Ð²Ð¸Ñ
                const unlock = () => {
                    video.play().finally(() => {
                        document.body.removeEventListener('touchstart', unlock)
                        document.body.removeEventListener('click', unlock)
                    })
                }
                document.body.addEventListener('touchstart', unlock, {
                    once: true,
                })
                document.body.addEventListener('click', unlock, { once: true })
            })
        }

        // ÐŸÑ€Ð¾Ð²ÐµÑ€ÑÐµÐ¼ Ð³Ð¾Ñ‚Ð¾Ð²Ð½Ð¾ÑÑ‚ÑŒ Ð²Ð¸Ð´ÐµÐ¾
        if (video.readyState >= 3) {
            tryPlay()
        } else {
            video.addEventListener('canplay', tryPlay, { once: true })
        }
    })
})
